n: 0;\n}\n\nol[role=\"list\"], ul[role=\"list\"] {\n  list-style: none;\n  padding-inline: 0;\n}\n\nh1, h2, h3, h4, h5 {\n  font-size: inherit;\n  font-weight: inherit;\n}\n\nimg {\n  display: block;\n  max-inline-size: 100%;\n}\n\ninput, button, textarea, select {\n  font: inherit;\n  font-size: 100%;\n  width: 150px;\n  padding: 0;\n  margin: 0;\n  box-sizing: border-box;\n}\n\np, h1, h2, h3, h4, h5, h6 {\n  overflow-wrap: break-word;\n}\n\nimg {\n  height: 500px;\n  width: auto;\n}\n\nheader {\n  display: flex;\n  justify-content: space-between;\n}\n\nul {\n  list-style: none;\n  margin: 0;\n  padding: 0;\n}\n\ninput[type='checkbox'] {\n  appearance: none;\n  height: 1.3rem;\n  min-height: 1.3rem;\n  width: 1.3rem;\n  min-width: 1.3rem;\n  margin: 0;\n  padding: 0;\n  border: solid var(--grey) 2px;\n  border-radius: 100px;\n  position: relative;\n  background-color: transparent;\n  cursor: pointer;\n}\n\ninput[type='checkbox']:checked {\n  background-color: var(--grey);\n}\n\ninput[type='checkbox']:checked::after {\n  content: \"\";\n  position: absolute;\n  left: 0.4rem;\n  top: 0.15rem;\n  width: 0.3rem;\n  height: 0.6rem;\n  border: solid var(--white);\n  border-width: 0 2px 2px 0;\n  transform: rotate(45deg);\n}\n\ntextarea {\n  resize: none;\n  width: 100%;\n  padding: 0px 5px 0px 5px;\n}\n\n* {\n  box-sizing: border-box;\n  /* border: solid red 0.1px; */\n}\n\n\n.bold {\n  font-weight: 600;\n}\n\n.remove-border-background {\n  background: none;\n  border: none;\n}\n\n.pointer {\n  cursor: pointer;\n}\n\n.layout {\n  display: flex;\n}\n\n.sidebar {\n  position: fixed;\n  height: 100vh;\n  width: 350px;\n  display: flex;\n  flex-direction: column;\n  flex: 1;\n  color: var(--black);\n  background-color: var(--uranian-blue);\n  font-size: 1.4rem;\n  border-right: solid 4px var(--black);\n  z-index: 100;\n}\n\nnav {\n  height: 80%;\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n}\n\n.logo-wrapper {\n  display: flex;\n  justify-content: center;\n  position: relative;\n  height: 125px;\n  padding: 10px;\n}\n\n.logo {\n  height: 100%;\n  width: auto;\n}\n\n.horizontal-divider,\n.vertical-divider {\n  background-color: var(--black);\n  border-radius: 20px;\n}\n\n.horizontal-divider {\n  height: 4px;\n}\n\n.vertical-divider {\n  width: 4px;\n}\n\n.nav-divider {\n  width: 90%;\n  align-self: center;\n}\n\n.main-divider {\n  width: 101%;\n  border-radius: 20px;\n  margin-left: -1%;\n  margin-bottom: 14px;\n}\n\n.links {\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  margin-bottom: 0px;\n}\n\n.link {\n  height: 100px;\n}\n\n.nav-icon {\n  height: 1.6rem;\n  align-self: center;\n  padding: 0;\n  margin: 0;\n}\n\n.nav-home,\n.nav-new-project {\n  display: flex;\n  height: 66px;\n  font-size: 1.7rem;\n  padding-left: 1rem;\n  align-items: center;\n}\n\n.nav-home > .nav-home-icon,\n.nav-new-project > .new-project-icon {\n  height: 2rem;\n  margin-right: 6px;\n}\n\n.nav-new-project {\n  position: relative;\n}\n\n.project-container {\n  display: grid;\n  height: 66px;\n  grid-template-columns: 1.8rem auto 1.8rem;\n  grid-template-rows: 1.8rem auto 1.8rem;\n  column-gap: 8px;\n  align-items: center;\n  padding: 0.4rem 0 0.1rem 1rem;\n}\n\n.nav-project-name,\n.nav-project-desc {\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  max-width: 600px;\n}\n\n.nav-project-desc {\n  grid-column-start: span 3;\n  grid-row-start: span 2;\n  font-size: 1rem;\n  max-width: 320px;\n}\n\n.active-project {\n  background-color: var(--aquamarine);\n  color: var(--black);\n}\n\n.active-project .nav-delete-icon svg {\n  fill: var(--aquamarine);\n}\n\n.nav-delete-icon {\n  align-self: end;\n  justify-self: end;\n}\n\n.nav-delete-icon svg {\n  fill: var(--uranian-blue);\n  transition: fill 0.5s ease;\n}\n\n.project:hover .nav-project-name,\n.project:hover .nav-project-desc {\n  color: var(--black);\n}\n\n.project:hover .nav-project-icon svg,\n.project:hover .nav-delete-icon svg {\n 